<template>
    <div id="map"></div>
    <div class="bot1">
        <b-button pill variant="primary" @click="displayOleum">오름</b-button>
        <b-button pill variant="primary" @click="displayOlle">제주올레</b-button>
        <b-button pill variant="primary" @click="displayHalla">한라산 둘레길</b-button>
        <b-button pill variant="primary" @click="displayHiking">등산로</b-button>
        <b-button pill variant="primary" @click="displayzpt">제로포인트루트</b-button>
    </div>
    <div class="bot2">
        <b-button pill variant="success" @click="displayCafe(true)">제주카페</b-button>
        <b-button pill variant="success" @click="displayRest(true)">제주식당</b-button>
        <b-button pill variant="success" @click="displayHotel(true)">제주숙박</b-button>
    </div>
    <!-- <div>
        <b-button-group size="sm">
        <b-button
                v-for="(btn, idx) in my_btns"
                :key="idx"
                :pressed="btn.state"
                pill
                variant="primary"
                @click="btn.func"
            >
            {{ btn.caption }}
        </b-button>
    </b-button-group>
    </div> -->
    <div class="bot3">
        <b-button pill variant="secondary" @click="getdataFromkakao">다음맵에서 가져오기</b-button>
        <b-button pill variant="secondary" @click="kakaodatamyfileWrite">다음맵에서 파일로 저장</b-button>
    </div>

</template>

<script>
import { toRaw } from "vue";
import {ref, watch, watchEffect} from 'vue';

import oleum_data from "../assets/jeju_oleum.json"
import olle_data from "../assets/jeju_olle_trail.json"
import halla_data from "../assets/jeju_halla_trail.json"
import hiking_data from "../assets/jeju_hiking_total.json"
import zpt_data from "../assets/jeju_zpt.json"

import jeju_rest_all_data from "../assets/jeju_rest_all.json"
import jeju_cafe_all_data from "../assets/jeju_cafe_all.json"
import hotel_all_data from "../assets/jeju_hotel_all.json"

var apikey= ref('');

var customOverlay=({});
var infoWindow=({});
var cataindex=0;
var hallaIndex=true;
var halla_polylines=[];
var olleIndex=true;
var olle_polylines=[];
var hikingIndex=true;
var hike_polylines=[];
var zptIndex=true;
var zpt_polylines=[];
var oleumIndex=true;
var oleum_circles=[];

var jejucafeIndex=true;
var jejucafeMarkers=[];
var seogwipocafeIndex=true;
var seogwipocafeMarkers=[];
var cafeIndex = true;

var jejurestIndex=true;
var jejurestMarkers=[];
var seogwiporestIndex=true;
var seogwiporestMarkers=[];
var restIndex = true;

var jejuhotelIndex=true;
var jejuhotelMarkers=[];
var seogwipohotelIndex=true;
var seogwipohotelMarkers=[];
var hotelIndex= true;

var searchData=[];
var changeMaptype;
var currentTypeId;

// var jejusi_index=false;
// var seogwiposi_index=false;

export default {
    name: "KakaoMap",
    
    props:{
        height:String,
        width:String,
    },

    data() {
        return {
            // jejusi_index:jejusi_index,
            // seogwiposi_index:seogwiposi_index,
            // my_btns:[
            //     { caption: '제주카페', state:false, func : this.displayCafe(true)},
            //     { caption: '제주식당', state:true , func : this.displayRest(true)},
            //     { caption: '제주숙박', state:false, func : this.displayHotel(true)},
            // ]                        
        };
    },

    created() {
        apikey = process.env.VUE_APP_KAKAOMAP_API_KEY;        
        // console.log('api_key ', apikey);
    },

    mounted() {
        if (!window.kakao || !window.kakao.maps) {            
            const script = document.createElement("script");
            script.onload = () => kakao.maps.load(this.initMap);
            script.src ="//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey="+apikey+'&libraries=services';
            document.head.appendChild(script);
        } else {
            this.initMap();            
        }            
    },

    methods: {

        initMap() {

            const container = document.getElementById("map");           
            container.style.width = `${this.width}`;
            container.style.height = `${this.height}`;            
            const options = {
                center: new kakao.maps.LatLng(33.38, 126.58),
                level: 10,
            };
            this.map = new kakao.maps.Map(container, options), 
            customOverlay = new kakao.maps.CustomOverlay({}),
            infoWindow = new kakao.maps.InfoWindow({});
            this.map.relayout();

            var mapTypeControl = new kakao.maps.MapTypeControl();
            this.map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);
            var zoomControl = new kakao.maps.ZoomControl();
            this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

            var content = document.createElement('div');
            content.className = 'modes';
            content.innerHTML =  
                // '<div class="modes">\n'+
                // '<button id="hybrid">하이브리드</button>\n' +
                '<button id="terrain">지형도</button>\n' +
                '<button id="traffic">교통</button>\n' +
                '<button id="roadview">도로뷰</button>\n' +
                '<button id="use_district">지적도</button>\n' +
                '<button id="jejusi">제주시</button>\n' +
                '<button id="seogwiposi">서귀포시</button>\n' +
                '</div>\n';
            container.appendChild(content);              
            // console.log(document);

            // const hybrid_btn = document.querySelector('#hybrid');  
            // hybrid_btn.addEventListener('click', (event) => {
            //     this.onSelectMap('hybrid')
            // });

            const terrain_btn = document.querySelector('#terrain');  
            terrain_btn.addEventListener('click', (event) => {
                this.onSelectMap('terrain')
            });

            const traffic_btn = document.querySelector('#traffic');  
            traffic_btn.addEventListener('click', (event) => {
                this.onSelectMap('traffic')
            });

            const roadview_btn = document.querySelector('#roadview');  
            roadview_btn.addEventListener('click', (event) => {
                this.onSelectMap('roadview')
            });

            const use_district_btn = document.querySelector('#use_district');  
            use_district_btn.addEventListener('click', (event) => {
                this.onSelectMap('use_district')
            });

            const jejusi_btn = document.querySelector('#jejusi');  
            jejusi_btn.addEventListener('click', (event) => {
                if ( !cafeIndex ) {
                    jejucafeIndex = jejucafeIndex ? false : true
                    seogwipocafeIndex = false
                    cafeIndex = false                                
                    this.displayCafe(false)
                }

                if ( !restIndex ) {
                    jejurestIndex = jejurestIndex ? false : true
                    seogwiporestIndex = false
                    restIndex = false                                
                    this.displayRest(false)
                }

                if ( !hotelIndex ) {
                    jejuhotelIndex = jejuhotelIndex ? false : true
                    console.log('jejuhotelIndex', jejuhotelIndex)
                    seogwipohotelIndex = false
                    hotelIndex = false                                
                    this.displayHotel(false)
                }               
                
            });

            const seogwiposi_btn = document.querySelector('#seogwiposi');  
            seogwiposi_btn.addEventListener('click', (event) => {
                
                if ( !cafeIndex ) {
                    seogwipocafeIndex = seogwipocafeIndex ? false : true
                    cafeIndex = false
                    jejucafeIndex = false
                    this.displayCafe(false)
                } 
                if ( !restIndex ) {
                    seogwiporestIndex = seogwiporestIndex ? false : true
                    restIndex = false
                    jejurestIndex = false
                    this.displayRest(false)
                } 
                if ( !hotelIndex ) {
                    seogwipohotelIndex = seogwipohotelIndex ? false : true
                    hotelIndex = false
                    jejuhotelIndex = false
                    this.displayHotel(false)
                }               
                
            });

            // console.log(div);
            // // toRaw(this.map).relayout();            
            this.make_cafe_data()
            this.make_rest_data()
            this.make_hotel_data()
        },

        make_cafe_data() {
            cataindex=10;
            let features = jeju_cafe_all_data.features;
            features.forEach( element => {
                let name = element.place_name;                
                let my_address = element.address_name;
                let city = element.address_name.split(' ')[1];
                let my_link = element.place_url;
                var position = new kakao.maps.LatLng(element.y, element.x);
                let phone = element.phone;                        
                this.citydrawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone, city);                
            });

        },

        make_rest_data() {
            cataindex=11;
            let features = jeju_rest_all_data.features;
            features.forEach( element => {   
                let name = element.place_name;                
                let my_address = element.address_name;
                let city = element.address_name.split(' ')[1];
                let my_link = element.place_url;
                var position = new kakao.maps.LatLng(element.y, element.x);
                let phone = element.phone;
                this.citydrawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone, city);                
            });

        },

        make_hotel_data() {
            cataindex=12;
            let features = hotel_all_data.features;            
            features.forEach( element => {   
                let name = element.place_name;                
                let my_address = element.address_name;
                let city = element.address_name.split(' ')[1];
                let my_link = element.place_url;
                var position = new kakao.maps.LatLng(element.y, element.x);
                let phone = element.phone;
                this.citydrawingMarker(cataindex, toRaw(this.map), position, name, my_address, my_link, phone, city);
            });

        },

        citydrawingMarker(cataindex, map, position, name, my_address, my_link, phone, city) {

            var imageSrc = '';
            
            if (cataindex == 10) {
                if ( city === '제주시') {
                    imageSrc = '/img/jeju_cafe.png';

                } else if ( city === '서귀포시') {
                    imageSrc = '/img/seogwipo_cafe.png';
                }                
            } else if (cataindex == 11) {
                if ( city === '제주시') {
                    imageSrc = '/img/jeju_rest.png';

                } else if ( city === '서귀포시') {
                    imageSrc = '/img/seogwipo_rest.png';
                }                
            } else if (cataindex ==12) {
                if ( city === '제주시') {
                    imageSrc = '/img/jeju_hotel.png';

                } else if ( city === '서귀포시') {
                    imageSrc = '/img/seogwipo_hotel.png';
                }
            }

            var imageSize = new kakao.maps.Size(16, 17);
            var imageOption = {offset: new kakao.maps.Point(8, 9)};            
            var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)
            var marker = new kakao.maps.Marker({
                position : position,
                clickable: true,
                image : markerImage,
                title : name,
            });


            // var content =  '<div style="width:250px; height: 150px; padding: 5px;">';
            //     content += '    <a href='+my_link+' target="_blank"><strong> [ 오름명 : '+name+']</strong></a>';
            //     content += '    <div> 주소 : '+my_address+' </div>';
            //     content += '</div>';
            
            // var content = '<div style="width:250px; height: 100px; padding: 5px;"> <div></div>' +
            var content = '<div class="placeinfo">'+
                '   <a class="title" href="' + my_link + '" target="_blank" title="' + name + '">' + name + '</a>';
                content += '<div> <span title="' + my_address + '">' + my_address + '</span> </div>';   
                content += '      <span class="tel"> Tel. ' + phone + '</span>'; 
                content += '<div class="after"></div>';

            // console.log(content);

            var iwRemveable = true;
            var infowindow = new kakao.maps.InfoWindow({
                content: content,
                removable: iwRemveable,
            });            
            new kakao.maps.event.addListener(marker, 'click', function() {                                  
                infowindow.open(map, marker);
            })

            if (cataindex ==10) {
                if (city === '제주시') jejucafeMarkers.push(marker)
                else if (city === '서귀포시') seogwipocafeMarkers.push(marker)
            } else if (cataindex ==11) {
                if (city === '제주시') jejurestMarkers.push(marker)
                else if (city === '서귀포시') seogwiporestMarkers.push(marker)
            } else if (cataindex ==12) {
                if (city === '제주시') jejuhotelMarkers.push(marker)
                else if (city === '서귀포시') seogwipohotelMarkers.push(marker)
            }            
        },

        displayCafe(main_index){
            // console.log(' cafeIndex ', cafeIndex, 'jejucafelIndex ',jejucafeIndex, 'seogwipocafeIndex ', seogwipocafeIndex  )
            if(cafeIndex){                
                if ( jejucafeIndex ) jejucafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                if ( seogwipocafeIndex ) seogwipocafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                cafeIndex = false
                jejucafeIndex = false
                seogwipocafeIndex = false
            } else {
                if (main_index) {
                    jejucafeMarkers.map((cur) => cur.setMap(null))
                    seogwipocafeMarkers.map((cur) => cur.setMap(null))
                    cafeIndex = true
                    jejucafeIndex = true
                    seogwipocafeIndex = true
                } else {
                    if ( jejucafeIndex ) jejucafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else jejucafeMarkers.map((cur) => cur.setMap(null))                    
                    if ( seogwipocafeIndex ) seogwipocafeMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else seogwipocafeMarkers.map((cur) => cur.setMap(null))
                }               
            }
        },

        displayRest(main_index){
            // console.log(' restIndex ', restIndex, 'jejurestIndex ',jejurestIndex, 'seogwiporestIndex ', seogwiporestIndex  )
            if(restIndex){                
                if ( jejurestIndex ) jejurestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                if ( seogwiporestIndex ) seogwiporestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                restIndex = false
                jejurestIndex = false
                seogwiporestIndex = false
            } else {
                if (main_index) {
                    jejurestMarkers.map((cur) => cur.setMap(null))
                    seogwiporestMarkers.map((cur) => cur.setMap(null))
                    restIndex = true
                    jejurestIndex = true
                    seogwiporestIndex = true
                } else {
                    if ( jejurestIndex ) jejurestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else jejurestMarkers.map((cur) => cur.setMap(null))                    
                    if ( seogwiporestIndex ) seogwiporestMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else seogwiporestMarkers.map((cur) => cur.setMap(null))
                }               
            }
        },

        displayHotel(main_index){
            // console.log(' hotelIndex ', hotelIndex, 'jejuhotelIndex ',jejuhotelIndex, 'seogwipohotelIndex ', seogwipohotelIndex  )
            if(hotelIndex){                
                if ( jejuhotelIndex ) jejuhotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                if ( seogwipohotelIndex ) seogwipohotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                hotelIndex = false
                jejuhotelIndex = false
                seogwipohotelIndex = false
            } else {
                if (main_index) {
                    jejuhotelMarkers.map((cur) => cur.setMap(null))
                    seogwipohotelMarkers.map((cur) => cur.setMap(null))
                    hotelIndex = true
                    jejuhotelIndex = true
                    seogwipohotelIndex = true
                } else {
                    if ( jejuhotelIndex ) jejuhotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else jejuhotelMarkers.map((cur) => cur.setMap(null))                    
                    if ( seogwipohotelIndex ) seogwipohotelMarkers.map((cur) => cur.setMap(toRaw(this.map)))
                    else seogwipohotelMarkers.map((cur) => cur.setMap(null))
                }               
            }
        },        

        onSelectMap: function(maptype) {
            if(maptype ==='roadmap') {
                if (currentTypeId) {
                    this.map.removeOverlayMapTypeId(currentTypeId);    
                }
                this.map.setMapTypeId(kakao.maps.MapTypeId.ROADMAP)
            } 
            // else if (maptype ==='hybrid') {
            //     this.map.setMapTypeId(kakao.maps.MapTypeId.HYBRID) 
            // } 
            else {
                
                if (maptype === 'traffic') {
                    changeMaptype = kakao.maps.MapTypeId.TRAFFIC;
                } else if (maptype === 'roadview') {
                    changeMaptype = kakao.maps.MapTypeId.ROADVIEW;
                } else if (maptype === 'terrain') {
                    changeMaptype = kakao.maps.MapTypeId.TERRAIN;
                } else if (maptype === 'use_district') {
                    changeMaptype = kakao.maps.MapTypeId.USE_DISTRICT;           
                }
                if (currentTypeId) {
                    this.map.removeOverlayMapTypeId(currentTypeId);    
                }
                this.map.addOverlayMapTypeId(changeMaptype);
                currentTypeId = changeMaptype;
            }
            
            // else if (maptype ==='terrain') {
            //     this.map.addOverlayMapTypeId(kakao.maps.MapTypeId.TERRAIN)
            //     this.map.removeOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC) 
            // } else if (maptype ==='traffic') {
            //     // const container = document.getElementById("map"); 
            //     // const now_center = this.map.getCenter()
            //     // const options = {
            //     //     center: new kakao.maps.LatLng(now_center.Ma, now_center.La),
            //     //     level: 5,
            //     // };
            //     // this.map = new kakao.maps.Map(container, options) 
            //     this.map.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC)
            // }            
        },

        displayOleum() {

            cataindex=1;
            if(oleumIndex) {

                if(oleum_circles.length <=0 ) {
                    let features = oleum_data.features;
                    features.forEach( element => {
                        let position = new kakao.maps.LatLng(element.geometry.coordinates[1], element.geometry.coordinates[0]);
                        let dia = element.properties['반경']; 
                        let name = element.properties['오름명']
                        this.drawingCircle(cataindex, toRaw(this.map), position, dia, name);                        
                    });                    
                } else {
                    oleum_circles.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                oleumIndex = false;
            } else {
                oleum_circles.map( (cur) => cur.setMap(null));
                oleumIndex = true;
            }
            
        },

        displayOlle() {

            cataindex=1;
            if(olleIndex) {
                if( olle_polylines.length <=0 ) {
                    let features = olle_data.features;            
                    features.forEach( element => {      
                        let name = element.properties.name;             
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex,toRaw(this.map), path, name);
                        });
                    });
                } else {
                    olle_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }                
                olleIndex = false;
            } else {
                olle_polylines.map( (cur) => cur.setMap(null));
                olleIndex = true;
            }            
        },

        displayHalla() {

            cataindex=2; 
            if(hallaIndex) {
                if ( halla_polylines.length <=0 ) {
                    let features = halla_data.features;            
                    features.forEach( element => { 
                        let name = element.properties.name;  
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);                     
                        });
                    });
                } else {
                    halla_polylines.map( (cur) => cur.setMap(toRaw(this.map)));
                }
                hallaIndex = false;
            } else {
                halla_polylines.map( (cur) => cur.setMap(null));
                hallaIndex = true;
            }
            
        },

        displayHiking() {

            cataindex=3;
            if(hikingIndex){
                if ( hike_polylines.length <=0 ) {                    
                    let features = hiking_data.features;
                    features.forEach( element => {                        
                        let name = '등산로 '+ element.properties.PMNTN_NM;                
                        let coord = element.geometry.coordinates;
                        coord.forEach( points => {
                            var path=[];
                            points.forEach ( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                        });
                    });
                } else {
                    hike_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                hikingIndex = false;
            } else {
                hike_polylines.map( (cur) => cur.setMap(null));
                hikingIndex = true;
            }          
        },

        displayzpt(){

            cataindex=4;
            if(zptIndex){
                if ( zpt_polylines.length <=0 ) {                    
                    let features = zpt_data.features;
                    features.forEach( element => {                        
                        let name = element.properties.name;                
                        let coord = element.geometry.coordinates;

                        if ( element.geometry.type=='LineString' ) {                            
                            
                            var path=[];                            
                            coord.forEach( point => {
                                let position = new kakao.maps.LatLng(point[1], point[0]);
                                path.push(position);              
                            });
                            this.drawingPolyline(cataindex, toRaw(this.map), path, name);

                        } else if (element.geometry.type=='MultiLineString'){
                            
                            coord.forEach( points => {
                                var path=[];
                                points.forEach ( point => {
                                    let position = new kakao.maps.LatLng(point[1], point[0]);
                                    path.push(position);
                                });
                                this.drawingPolyline(cataindex, toRaw(this.map), path, name);            
                            });

                        }
                    });
                } else {
                    zpt_polylines.map( (cur) => cur.setMap(toRaw(this.map)));                    
                }
                zptIndex = false;
            } else {
                zpt_polylines.map( (cur) => cur.setMap(null));
                zptIndex = true;
            }

        },

        drawingCircle(cataindex, map, position, dia, name) {
            
            let fillrealcolor='';
            let backcolor='';
            
            if (cataindex==1) {
                fillrealcolor = '#E51D1A';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                fillrealcolor = '#00FF0F';
                backcolor = '#FF0000';      
            } else if (cataindex==3) { 
                fillrealcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                fillrealcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var circle = new kakao.maps.Circle({
                    map: map,
                    center : position,
                    radius: dia,    
                    strokeColor: '#5347AA',
                    strokeOpacity: 0.5,
                    strokeWeight: 2,
                    fillColor: fillrealcolor,
                    fillOpacity: 0.3,
                    // 원의 중심좌표 입니다 
