        drawingPolyline(cataindex, map, path, name) {

            let realcolor='';
            let backcolor='';

            if (cataindex==1) {
                realcolor = '#0004FF';
                backcolor = '#FF0000';      
            } else if (cataindex==2) {
                realcolor = '#00FF0F';
                backcolor = '#FF0000';      
            } else if (cataindex==3) { 
                realcolor = '#FF00FF';
                backcolor = '#0000FF';                      
            }  else if (cataindex==4) { 
                realcolor = '#FF0000';
                backcolor = '#0000FF';
            }

            var polyline = new kakao.maps.Polyline({
                map: map,
                path: path, // 선을 구성하는 좌표배열 입니다
                strokeWeight: 2, // 선의 두께 입니다
                strokeColor: realcolor, // 선의 색깔입니다
                strokeOpacity: 0.5, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                strokeStyle: 'solid'                 
            });            
            
            var infoBox = new kakao.maps.CustomOverlay({
                content: name,
                yAnchor: 1,
            });

            new kakao.maps.event.addListener(polyline, 'mouseover', function(mouseEvent) {                                  
                polyline.setOptions({
                    strokeWeight: 7,
                    strokeColor: backcolor,
                    strokeOpacity: 1,
                    strokeStyle: 'solid'
                });
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
                // console.log('ddd ', infoBox);
            })
            
            new kakao.maps.event.addListener(polyline, 'mousemove', function(mouseEvent) {                
                // customOverlay.setPosition(mouseEvent.latLng); 
                infoBox.setPosition(mouseEvent.latLng);
                infoBox.setMap(map);
            });
            
            new kakao.maps.event.addListener(polyline, 'mouseout', function() {                
                polyline.setOptions({
                    strokeWeight: 2, // 선의 두께 입니다
                    strokeColor: realcolor, // 선의 색깔입니다
                    strokeOpacity: 0.5, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
                    strokeStyle: 'solid' 
                })
                infoBox.setMap(null);   
            });

            if (cataindex==1) {
                olle_polylines.push(polyline);
            } else if (cataindex==2) {
                halla_polylines.push(polyline);
            } else if (cataindex==3) { 
                hike_polylines.push(polyline);
            } else if (cataindex==4) { 
                zpt_polylines.push(polyline);
            }  
        },

